<template>
    <div class="text-center">
        <v-bottom-sheet v-model="sheet" inset hide-overlay>
            <v-sheet style="height: fit-content">
                <v-card v-if="bottomPopupLoadingFlag"
                        class="mx-auto"
                        outlined
                >
                    <div class="text-center ma-5" >
                    <v-progress-circular
                            :size="70"
                            :width="7"
                            color="green"
                            indeterminate
                    ></v-progress-circular>
                    </div>
                </v-card>

                <v-card v-else
                        class="mx-auto"
                        outlined
                >
                    <div class="text-right">
                        <v-btn
                                class="ma-2"
                                text
                                color="error"
                                @click="closeClicked"
                        >close</v-btn>
                    </div>
                    <v-list-item-title class="text-center">Organization Name</v-list-item-title>
                    <v-card-text>
                        <v-simple-table>
                            <template v-slot:default>
                                <tbody>
                                <tr>
                                    <td>Location:</td>
                                    <td>149, Abed Dhali Road, Lake Circus, Kalabagan, Dhaka - 1205</td>
                                </tr>
                                <tr>
                                    <td>Relief Type:</td>
                                    <td>Food</td>
                                </tr>
                                <tr>
                                    <td>Position:</td>
                                    <td>latitude: {{lat}}, longitude:{{lng}}</td>
                                </tr>
                                </tbody>
                            </template>
                        </v-simple-table>
                    </v-card-text>

                    <div class="text-center pb-2">
                        <v-btn rounded class="green darken-1" small dark @click="seeMoreClicked" :loading="seeMoreLoadingFlag">See More</v-btn>
                    </div>

                </v-card>
            </v-sheet>
        </v-bottom-sheet>
    </div>
</template>

<script>
    export default {
        name: "BottomPopup",
        data(){
            return{
                sheet:true,
                seeMoreLoadingFlag: false,
                bottomPopupLoadingFlag: true,

                lat: "null",
                lng: "null",
            }
        },
        watch:{
          'sheet'(to,from){
              console.log("sheet set to false");
              // this.sheet= to;
              setTimeout(()=>{
                  this.$router.push('/search');
              },200);
          }
        },
        mounted() {
            let position = JSON.parse(this.$route.params.position);
            console.log(position);
            console.log("BottomPopup mounted");
            setTimeout(()=>{
                this.lat = position.lat;
                this.lng = position.lng;
              this.bottomPopupLoadingFlag=false;
          },2000);

        },
        methods:{
            closeClicked(){
                console.log('close clicked');
                this.sheet= !this.sheet;
                //setTimeout(()=>{
                    //this.$router.push('/search');
                //},200);

            },
            seeMoreClicked(){
                this.seeMoreLoadingFlag=true;
                setTimeout(()=>{
                    this.seeMoreLoadingFlag=false;
                },3000);
            }
        },

    }
</script>

